<template>
  <div>
    <h3>{{ id ? "" : "" }}</h3>
    <el-form
      label-width="120px"
      @submit.native.prevent="save"
      style="width:45rem;"
    >
      <el-form-item label="">
        <el-input v-model="admin.username"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="admin.email"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="admin.password"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-switch v-model="admin.is_super"> </el-switch>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: {}
  },
  data() {
    return {
      admin: {
        eamil: "",
        password: ""
      }
    };
  },
  methods: {
    async save() {
      if (this.id) {
        await this.$http.put(`/admins/${this.id}`, this.admin);
      } else {
        await this.$http.post("/admins", this.admin);
      }
      this.$router.push("/admin/list");
      this.$message({
        type: "success",
        message: ""
      });
    },
    async fetch() {
      const res = await this.$http.get(`/admins/${this.id}`);
      this.admin = res.data;
      if (res.data.is_super == 1) {
        this.admin.is_super = true;
      } else {
        this.admin.is_super = false;
      }
      this.admin = Object.assign({}, this.admin, res.data); // this.admin{}res.data{}
    }
  },
  created() {
    this.id && this.fetch();
  }
};
</script>

